.customDropdown select {
	cursor:pointer;
	background-color: #e7ecf0;
	border: 2px solid rgba(0, 0, 0, 0.479);
	color: #000;
	font-size: inherit;
	padding: .5em;
	padding-right: 2.5em;	
	margin: 0;
	border-radius: 6px;
	text-indent: 0.01px;
	text-overflow: '';
	-webkit-appearance: button; /* hide default arrow in chrome OSX */
  }
  
