.myModal {
	position: fixed;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	display: none;
	background: rgba(0, 0, 0, 0.5);
	z-index: 3;
} 

.myModalContent {
	position: absolute;
	padding: 25px;
	background: white;
	border-radius: 16px;
	min-width: 370px;
	z-index: 3;
}


.myModal.active {
	display: flex;
	justify-content: center;
	align-items: center;

}